import React from "react";
import ReactDOM from "react-dom";

import SlideTransition from "../.././../shared/components/Transitions/SlideTransition";
import "./DrawerProfile.css";
import LinksDrawer from "./LinksDrawer";

const DrawerProfile = (props) => {
  const profile_links = ["profile", "cours", "bibliotheque", "quiz"];
  const actions_links = ["deconnexion"];
  const drawerProfile = (
    <SlideTransition show={props.show} timeout={400}>
      <aside className="drawer-profile">
        <div className="drawer-profile__content">
          <div className="drawer-profile__profile">
            <div className="drawer-profile__avatar"></div>
            <div className="drawer-profile__info">
              <h2>Yassine DJEBBARI</h2>
              <h3>CEM 4ème année</h3>
              <p>ABONNEMENT ACTIF</p>
            </div>
          </div>

          <LinksDrawer
            links={profile_links}
            className="drawer-profile__links"
          />
          <LinksDrawer
            links={actions_links}
            className="drawer-profile__actions"
          />
        </div>
        <div className="drawer-profile__ad"></div>
      </aside>
    </SlideTransition>
  );

  return ReactDOM.createPortal(
    drawerProfile,
    document.getElementById("drawerProfile-hook")
  );
};

export default DrawerProfile;
